
.topinfo-wrap {
  height: 82rem;
  overflow-y: scroll;
  background-color: #e0e0e0;
}
.topinfo {
  width: 100%;
  height: 54rem;
  position: relative;
}
.prev-toplist {
  font-size: 3rem;
  line-height: 4rem;
  /*width: 100%;*/
  background-color: #e0e0e0;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 10;
}
.topinfo .topinfo-text {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 20%;
  padding-top: 40%;
  background-image: -webkit-linear-gradient(top, transparent, #e0e0e0 80%);
}
.topinfo-text h2 {
  font-size: 3.5rem;
  line-height: 5rem;
  height: 5rem;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  padding-left: 1rem;
}
.topinfo-text p {
  font-size: 2rem;
  line-height: 3rem;
  height: 3rem;
  padding-left: 1rem;
}
.topinfo-list {
  background-color: #e0e0e0;
  border-top: 1px solid rgba(0, 0, 0, 0.4);
}
.topinfo-list li {
  height: 9rem;
}
.topinfo-list .top-list-num {
  width: 7rem;
  height: 100%;
  float: left;
  text-align: center;
  line-height: 7rem;
  font-size: 3rem;
}
.topinfo-list .top-list-info {
  width: 47rem;
  height: 100%;
  float: left;
  box-sizing: border-box;
  border-bottom: 1px solid rgba(0, 0, 0, 0.4);
  padding-right: 2rem;
  overflow: hidden;
}
.topinfo-list .top-list-info h3 {
  font-size: 3.5rem;
  line-height: 5rem;
  font-weight: 400;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.topinfo-list .top-list-info p {
  font-size: 2.5rem;
  line-height: 4rem;
  color: grey;
  overflow: hidden;
}



