
.commend-list{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
.commend-list li{
     margin-bottom: 2rem;
     width: 45%;
     background-color: #fff;
}
.list_media{
    position: relative;
}
.list_media img{
    width: 100%;
}
.list_media .listen_count{
    position: absolute;
    left: 1rem;
    bottom: 1rem;
    color: #fff;
    font-size: 1.8rem;
    padding-left: 2.5rem;
    line-height: 2rem;
}
.list_media .listen_count i{
    position: absolute;
    left: 0;
    top:0.25rem;
    width: 1.5rem;
    height: 1.5rem;
    background: url(../../../assets/img/playnum.png) no-repeat;
    background-size: 100%;
}
.list_media .icon_play{
    position: absolute;
    right: 1rem;
    bottom: 0.5rem;
    width: 4rem;
    height: 4rem;
    background: url(../../../assets/img/play.png) no-repeat;
    background-size: 100%;
}
.list-info{
    padding: 0 7px 5px;
}
.songName{
    color: #666;
    font-size: 1.2rem;
    margin: 5px 0;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    font-weight: normal;
    font-size: 2.3rem;
}
.singer{
    color: #00e09e;
    font-size: 2rem;
}
.goToSeniority{
    margin-top: 20px;
    display: block;
    text-align: center;
    color: #999;
}



