
.toplist{
  padding: 1rem;
}
.toplist li{
  overflow: hidden;
  margin-bottom: 1rem;
  background-color: #fff;
}
.toplist img,.toplist div{
  float: left;
  height: 15rem;
}
.toplist img{
  width: 15rem;
}
.toplist div{
  width: 34rem;
  margin-left: 2rem;
}
.toplist h3{
  font-size: 2.5rem;
  line-height: 4rem;
  margin-bottom: 1.5rem;
}
.toplist h3,.toplist p{
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-weight: normal;
}
.toplist p{
  height: 2.5rem;
  line-height: 2.5rem;
  font-size: 2rem;
  margin-bottom: 0.5rem;
  color: rgba(0,0,0,.5);
}
.toplist p span{
  color: #000;
}



