







































































/*input[type="search"]{-webkit-appearance:none;} */
input::-webkit-search-cancel-button {display: none;
}
#search-wrap{
  width: 100%;
  height: 100%;
  overflow-y: auto;
  background-color: #fff;
  padding-top: 8rem;
  box-sizing: border-box;
}
#search{
  width: 100%;
  height: 8rem;
  box-sizing: border-box;
  padding: 1.5rem; 
  background-color: #ebeff4;
  position: fixed;
  left: 0;
  top: 12rem;
}
.search_input{
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  border:  none;
  font: 2.5rem/1 arial;
  border-radius: 0.5rem;
  padding: 0 5rem 0 3rem;
}
.search-btn{
  position: absolute;
  top: 2.5rem;
  right: 2.5rem;
  width: 3rem;
  height: 3rem;
  background: url(../../assets/img/search.png) no-repeat;
  background-size:100%; 
  border:none;
}
ul.hot{
  overflow: hidden;
  padding: 1.5rem;
}
ul.hot li{
  height: 3rem;
  font: 2rem/3rem arial;
  float: left;
  border: 1px solid #000; 
  border-radius: 1.5rem;
  padding: 0 1rem;
  margin: 1rem;
}
ul.hot li.hot{
  border-color: red;
}
ul.hot li.hot a{
  color: red;
}
.search-list{
  width: 80%;
  margin: 0 auto;
}
.search-list li{
  border-bottom: 1px solid #000;
  overflow: hidden;
  padding: 0.5rem 0;
}
.search-list div{
  float: left;
}
.search-list img{
  width: 8rem;
  height: 8rem;
}
.search-list h3{
  height: 5rem;
  line-height: 5rem;
  font-size: 2.5rem;
}
.search-list p{
  height: 3rem;
  line-height: 3rem;
  font-size: 2rem;
}
.search-list-info{
  margin-left: 1.5rem
}
#prev{
  position: fixed;
  left: 1rem;
  top: 23rem;
  width: 3rem;
  height: 3rem;
  font-size: 3rem
}



