
.play-bar{
    position: fixed;
    left:0;
    bottom: 0;
    width: 100%;
    height: 14rem;
    background-color: #fff;
}
.play-bar div{
    height: 100%;
    font-size: 2.5rem;
    float: left;
}
.music-list-btn{
  height: 4rem;
  font-size: 3rem;
}
#play{
  width: 100%;
  height: 5rem;
}
.play-l{
  width: 10rem;
}
.play-l img{
  width: 8rem;
  height: 8rem;
  margin: 3rem 1rem;
}
.play-m{
  width: 40rem;
}
.play-info{
  width: 100%;
  height: 4rem;
  line-height: 4rem;
  margin: 1rem 0;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.play-m .play-btns{
  width: 100%;
  height: 5rem;
  line-height: 5rem;
  font-size: 4rem;
}
.play-m .play-btns span{
  float: left;
  margin: 0 3rem;
}
.play-bar .play-list{
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 40rem;
  z-index: 20;
  background-color: rgba(0,0,0,.7);
  color: #fff;
}
.play-list-top{
  font: 3rem/5rem arial;
  text-align: center;
  border-bottom: 1px solid #000;
}
.play-bar .play-list li{
  font: 2.5rem/4rem arial;
  border-bottom: 1px solid #000;
  padding: 1rem;
  height: 4rem;
}
.play-list li p{
  float: left;
  width: 85%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.play-list ul{
  height: 30rem;
  overflow-y: auto;
  border-bottom: 1px solid #000;
}
.play-list li i{
  float: right;
  color: #000;
  font-size: 3rem;
  margin-right: 1rem;
}
.play-list .close{
  width: 100%;
  height: 5rem;
  line-height: 5rem;
  text-align: center;
}
.color{
  color: #00e09e;
}



