
header{
    width: 100%;
    height: 12rem;
    
    /*position: absolute;
    left: 0;
    top: 0;*/
}
h1,.top-navs{
    width: 100%;
    height: 50%;
    line-height: 6rem;
}
.logo{
    background-color: #31c27c;
}
.logo i{
    float: left;
    width: 13.5rem;
    height: 0;
    padding-top: 3.75rem;
    overflow: hidden;
    margin: 1.5rem 0 0 1.5rem;
    background: url(../../assets/img/logo.png) no-repeat 0 0/100%;
}
header h1{
    text-align: center;
    font-size: 3rem;
}
.top-navs{
    display:-webkit-box;
    display:-ms-flexbox;
    display:flex;
}
.top-navs li{
    height: 100%;
    width: 33.333%;
    box-sizing: border-box;
    text-align: center;
    font-size: 2.5rem;
}
.top-navs .router-link{
    border-bottom: 0.2rem solid #31c27c;
    color: #31c27c;
}



